"use client";
import { useState } from "react";

export default function SignupForm() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [role, setRole] = useState("user");
  const [errors, setErrors] = useState<{
    email?: string;
    password?: string;
    confirmPassword?: string;
  }>({});

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const newErrors: typeof errors = {};

    // Email validation
    if (!email.trim()) newErrors.email = "Email is required";
    else {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(email)) newErrors.email = "Invalid email format";
    }

    // Password validation
    if (!password.trim()) newErrors.password = "Password is required";
    else if (password.length < 6)
      newErrors.password = "Password must be at least 6 characters";

    // Confirm password validation
    if (confirmPassword !== password)
      newErrors.confirmPassword = "Passwords do not match";

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    setErrors({}); // clear errors

    try {
      // Call your API to create user
      /*
      await createUser({
        email,
        password,
        role
      });
      */

      console.log({ email, password, role });
    } catch (error) {
      console.error("Signup failed:", error);
    }
  };

  return (
    <div className="w-screen h-full flex flex-col items-center justify-center bg-[#111111]">
      <div className="w-85 mb-5 flex flex-col items-start justify-center">
        <span className="font-semibold text-[20px]">Sign Up for TerraFuel</span>
      </div>

      <div className="w-85">
        <form onSubmit={handleSubmit} className="flex flex-col gap-4">
          {/* Role Dropdown */}
          <div>
            <label className="block text-[12px] font-normal mb-1 text-[#EDEDED]">
              Role
            </label>
            <CommonDropdown
                value={role}
                onChange={setRole}
                options={[
                    { label: "User", value: "user" },
                    { label: "Admin", value: "admin" },
                ]}
                placeholder="Select Role"
                />
          </div>

          {/* Email */}
          <div>
            <label className="block text-[12px] font-normal mb-1 text-[#EDEDED]">
              Email
            </label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="your@email.com"
              className={`w-full rounded-[5px] border px-3 py-2 text-[14px] focus:outline-none ${
                errors.email ? "border-red-500" : "border-[#2A2A2A]"
              }`}
            />
            <p className="text-red-500 text-xs mt-1">{errors.email || ""}</p>
          </div>

          {/* Password */}
          <div>
            <label className="block text-[12px] font-normal mb-1 text-[#EDEDED]">
              Password
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="password"
              className={`w-full rounded-[5px] border px-3 py-2 text-[14px] focus:outline-none ${
                errors.password ? "border-red-500" : "border-[#2A2A2A]"
              }`}
            />
            <p className="text-red-500 text-xs mt-1">{errors.password || ""}</p>
          </div>

          {/* Confirm Password */}
          <div>
            <label className="block text-[12px] font-normal mb-1 text-[#EDEDED]">
              Confirm Password
            </label>
            <input
              type="password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="confirm password"
              className={`w-full rounded-[5px] border px-3 py-2 text-[14px] focus:outline-none ${
                errors.confirmPassword ? "border-red-500" : "border-[#2A2A2A]"
              }`}
            />
            <p className="text-red-500 text-xs mt-1">{errors.confirmPassword || ""}</p>
          </div>

          <button
            type="submit"
            className="w-full text-[14px] bg-[#FFFFFF] text-black rounded-[5px] py-2 font-medium hover:bg-blue-700"
          >
            Sign Up
          </button>
        </form>

        {/* Links */}
        <div className="mt-6 flex flex-col items-start gap-2 text-[12px]">
          <p>
            Already have an account?{" "}
            <a href="/" className="text-[#A259FF] hover:underline">
              Sign in
            </a>
          </p>
        </div>
      </div>
    </div>
  );
}
